body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.search-form {
  text-align: center;
  margin-top: 20px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

.photo-card {
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 10px;
  box-shadow: 0 2px 4px #0000001a;
}

.photo-card img {
  width: 100%;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.info {
  padding: 10px;
}

.info-item {
  margin: 5px 0;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  display: block;
}

.load-more:hover {
  background-color: #0056b3;
}

